'use client'

import { useState } from 'react'
import CloseIcon from '@mui/icons-material/Close'
import AttachFileIcon from '@mui/icons-material/AttachFile'
import { AnimatePresence } from 'framer-motion'

import {
  ModalContainer,
  ModalContent,
  ModalTitle,
  CloseButton,
  DropZone,
  DropZoneText,
  UploadButton,
  MovieTitleInput,
  LoadingContainer,
  LoadingText,
  ProgressBar,
  Progress,
  CancelButton,
} from './styles'

interface AddMovieModalProps {
  isOpen: boolean
  onClose: () => void
}

export function AddMovieModal({ isOpen, onClose }: AddMovieModalProps) {
  const [movieTitle, setMovieTitle] = useState('')
  const [file, setFile] = useState<File | null>(null)
  const [isUploading, setIsUploading] = useState(false)
  const [uploadProgress, setUploadProgress] = useState(0)

  const handleUpload = () => {
    setIsUploading(true)
    // Simulamos la carga
    let progress = 0
    const interval = setInterval(() => {
      progress += 10
      setUploadProgress(progress)
      if (progress >= 100) {
        clearInterval(interval)
        // Aquí manejaríamos la finalización de la carga
      }
    }, 500)
  }

  const handleCancel = () => {
    setIsUploading(false)
    setUploadProgress(0)
  }

  return (
    <AnimatePresence>
      {isOpen && (
        <ModalContainer
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
        >
          <ModalContent
            initial={{ y: 20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            exit={{ y: 20, opacity: 0 }}
          >
            <CloseButton onClick={onClose}>
              <CloseIcon />
            </CloseButton>
            <ModalTitle>AGREGAR PELÍCULA</ModalTitle>

            {!isUploading ? (
              <>
                <DropZone>
                  <AttachFileIcon />
                  <DropZoneText>
                    AGREGÁ UN ARCHIVO O ARRASTRALO Y SOLTALO AQUÍ
                  </DropZoneText>
                </DropZone>

                <MovieTitleInput
                  placeholder="TÍTULO"
                  value={movieTitle}
                  onChange={(e) => setMovieTitle(e.target.value)}
                  variant="standard"
                  InputProps={{
                    disableUnderline: true,
                  }}
                />

                <UploadButton 
                  disabled={!file || !movieTitle}
                  onClick={handleUpload}
                >
                  SUBIR PELÍCULA
                </UploadButton>
              </>
            ) : (
              <LoadingContainer>
                <LoadingText>CARGANDO {uploadProgress}%</LoadingText>
                <ProgressBar>
                  <Progress progress={uploadProgress} />
                </ProgressBar>
                <CancelButton onClick={handleCancel}>
                  CANCELAR
                </CancelButton>
              </LoadingContainer>
            )}
          </ModalContent>
        </ModalContainer>
      )}
    </AnimatePresence>
  )
} 